import React from 'react';
import {connect} from 'react-redux';
import classnames from 'classnames';
import JSONP from 'jsonp';
import ColorOptions from 'components/Customizer';
import ToggleDisplay from 'react-toggle-display';
import { Row, Col, getRowProps, getColumnProps } from 'react-flexbox-grid';


import Button from 'material-ui/Button';
import Menu, { MenuItem } from 'material-ui/Menu';
import Divider from 'material-ui/Divider';
import Drawer from 'material-ui/Drawer';
import Avatar from 'material-ui/Avatar';
//import AutoComplete from 'material-ui/AutoComplete';




const googleAutoSuggestURL = `//suggestqueries.google.com/complete/search?client=youtube&ds=yt&q=`;

class HeaderRight extends React.Component {
	state = {
	    index: 0,
    	anchorEl: undefined,
    	open: false,
  	};

  	button = undefined;

	handleChange = (event, index) => {
		this.setState({ index });
	};

	handleChangeIndex = (index) => {
		this.setState({ index });
	};

	handleOpen = (event) => {
    	this.setState({ open: true, anchorEl: event.currentTarget });
  	};

  	handleRequestClose = () => {
    	this.setState({ open: false });
  	};

	constructor(props) {
	    super(props);
		this.onUpdateInput = this.onUpdateInput.bind(this);
		this.state = {
	      	dataSource : [],
	      	inputValue : '',
			show: false,
			open: false
		}
	}

	handleToggle = () => this.setState({
		open: !this.state.open
	});

  	handleClose = () => this.setState({
		open: false
	});

	onUpdateInput(inputValue) {
		const self = this;
	    this.setState({
      		inputValue: inputValue
	    }, function() {
      		self.performSearch();
	    });
	}

	performSearch() {
	    const
      		self = this,
      		url  = googleAutoSuggestURL + this.state.inputValue;

	    if(this.state.inputValue !== '') {
      		JSONP(url, function(error, data) {
				let searchResults, retrievedSearchTerms;

        		if(error) return error;

        		searchResults = data[1];

        		retrievedSearchTerms = searchResults.map(function(result) {
          			return result[0];
        		});

        		self.setState({
          			dataSource: retrievedSearchTerms
        		});
      		});
	    }
  	}

	handleClick() {
	    this.setState({
	      	show: !this.state.show
	    });
  	}

	render() {
    	const { colorOption } = this.props;

		return (
			<Row>
				<ToggleDisplay show={!this.state.show}>
					<ul className="list-unstyled">
						<li>
							<Button
								aria-owns="simple-menu"
								aria-haspopup="false"
								onClick={this.handleClick}
								className = "user">
								<span>
									<Avatar className="md-initials">LS</Avatar>
									<span className="user-name">Luis Silva</span>
									<span><i className="material-icons dropdown-icon">keyboard_arrow_down</i></span>
								</span>
							</Button>
							<Menu
								id="simple-menu"
								anchorEl={this.state.anchorEl}
								open={this.state.open}
								onRequestClose={this.handleRequestClose}>
								<MenuItem onClick={this.handleRequestClose}
									className={classnames('menu-item', {
										'bg-color-medlight': ['11', '12', '13', '14', '15', '16'].indexOf(colorOption) >= 0,
										'bg-color-meddark': ['21', '22', '23', '24', '25', '26'].indexOf(colorOption) >= 0
									})}>
									<i className= "material-icons user-menu-icon"> assignment_ind </i>
									User Profile
								</MenuItem>
								<Divider inset />

								<MenuItem onClick={this.handleRequestClose}
									className={classnames('menu-item', {
										'bg-color-medlight': ['11', '12', '13', '14', '15', '16'].indexOf(colorOption) >= 0,
										'bg-color-meddark': ['21', '22', '23', '24', '25', '26'].indexOf(colorOption) >= 0
									})}>
									<i className= "material-icons user-menu-icon"> poll </i>
									User Activity
								</MenuItem>
								<Divider inset />

								<MenuItem onClick={this.handleRequestClose}
									className={classnames('menu-item', {
										'bg-color-medlight': ['11', '12', '13', '14', '15', '16'].indexOf(colorOption) >= 0,
										'bg-color-meddark': ['21', '22', '23', '24', '25', '26'].indexOf(colorOption) >= 0
									})}>
									<i className= "material-icons user-menu-icon"> settings_ethernet </i>
									Change Roles
								</MenuItem>
								<Divider inset />

								<MenuItem onClick={this.handleRequestClose}
									className={classnames('menu-item', {
										'bg-color-medlight': ['11', '12', '13', '14', '15', '16'].indexOf(colorOption) >= 0,
										'bg-color-meddark': ['21', '22', '23', '24', '25', '26'].indexOf(colorOption) >= 0
									})}>
									<i className= "material-icons user-menu-icon"> photo_library </i>
									Workgroups
								</MenuItem>
								<Divider inset />

								<MenuItem onClick={this.handleRequestClose}
									className={classnames('menu-item', {
										'bg-color-medlight': ['11', '12', '13', '14', '15', '16'].indexOf(colorOption) >= 0,
										'bg-color-meddark': ['21', '22', '23', '24', '25', '26'].indexOf(colorOption) >= 0
									})}>
									<i className= "material-icons user-menu-icon"> settings_input_antenna </i>
									Switch Networks
								</MenuItem>
								<Divider inset />

								<MenuItem onClick={this.handleRequestClose}
									className={classnames('menu-item', {
										'bg-color-medlight': ['11', '12', '13', '14', '15', '16'].indexOf(colorOption) >= 0,
										'bg-color-meddark': ['21', '22', '23', '24', '25', '26'].indexOf(colorOption) >= 0
									})}>
									<i className= "material-icons user-menu-icon"> settings </i>
									User Settings
								</MenuItem>
								<Divider inset />

								<MenuItem onClick={this.handleRequestClose}
									className={classnames('menu-item', {
										'bg-color-medlight': ['11', '12', '13', '14', '15', '16'].indexOf(colorOption) >= 0,
										'bg-color-meddark': ['21', '22', '23', '24', '25', '26'].indexOf(colorOption) >= 0
									})}>
									<i className= "material-icons user-menu-icon"> color_lens </i>
									Themes
								</MenuItem>
								<Divider inset />

								<MenuItem onClick={this.handleRequestClose}
									value="/login"
									className={classnames('menu-item', {
										'bg-color-medlight': ['11', '12', '13', '14', '15', '16'].indexOf(colorOption) >= 0,
										'bg-color-meddark': ['21', '22', '23', '24', '25', '26'].indexOf(colorOption) >= 0
									})}>
									<i className= "material-icons user-menu-icon"> power_settings_new </i>
									Log Out
								</MenuItem>
							</Menu>
						</li>
					</ul>
				</ToggleDisplay>
				<ToggleDisplay show={this.state.show}>
					<AutoComplete
						className="autocomplete"
          				hintText="Type anything"
						dataSource    = {this.state.dataSource}
						onUpdateInput = {this.onUpdateInput}
						underlineShow={false}
						style={{
								position: 'absolute',
								top: '0',
							    right: '114px',
							    bottom: '0',
							    left: '280px'
						}}
						/>
				</ToggleDisplay>
				<span className="open-search">
					<a href="javascript:;" onClick={ () => this.handleClick() }>
						<i className="material-icons">search</i>
					</a>
				</span>

				<Drawer
					width={300}
					docked={false}
					openSecondary={true}
					open={this.state.open}
					className="customizer"
					onRequestChange={(open) => this.setState({open})}>
					<ColorOptions />
		        </Drawer>
			</Row>
		);
	}
}

const mapStateToProps = state => ({
    colorOption: state.settings.colorOption
});


module.exports = connect(
    mapStateToProps
)(HeaderRight);



